@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&family=Poppins:wght@500&display=swap');

html, body, * {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.app-container {
	max-width: 672px;
	width: 100%;
	padding: 0 16px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%)
}

.header {
	background-color: #678ffe;
	padding: 10px;
	text-align: center;
	border-top-left-radius: 20px;
	border-bottom: 5px solid #091034;
}

.header h1 {
	font-family: 'Fredoka One', cursive;
	font-size: 32px;
	font-weight: 400;
	color: #091034;
}

.todo-container {
	padding: 5px 0;
}

.todo-item {
	padding: 10px 35px;
	font-family: 'Poppins', sans-serif;
	font-size: 18px;
	font-style: italic;
	line-height: 1.3;
	position: relative;
}

.todo-item:nth-child(even) {
	background-color: #dde3fd;
}

.todo-item button {
	border-radius: 2px;
	display: flex;
	align-items: center;
	justify-content: center;
	line-height: 1;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	cursor: pointer;
}

.todo-item button:hover {
	opacity: 0.75;
}

.todo-item button:focus {
	outline: none;
}

.check-btn {
	width: 20px;
	height: 20px;
	left: 10px;
	color: #ea202c;
	background-color: transparent;
	border: 1px solid #091034;
	transition: font 0.3s linear;
}

.delete-btn {
	width: 25px;
	height: 25px;
	font-size: 20px;
	color: #fff;
	right: 10px;
	padding-bottom: 1px;
	background-color: #ea202c;
	border: none;
}

.add-todo-container {
	padding: 15px;
	background-color: #091034;
	border-bottom-right-radius: 20px;
}

.add-todo-container input {
	padding: 4px 10px;
	border: none;
	border-radius: 0;
	border-bottom: 1px solid #dde3fd;
	font-family: 'Poppins', sans-serif;
	font-size: 16px;
	color: #dde3fd;
	background-color: transparent;
	width: calc(100% - 100px);
}

.add-todo-container input:focus {
	outline: none;
	background-color: rgba(255, 255, 255, 0.1);
}

.add-btn {
	font-family: 'Poppins', sans-serif;
	font-size: 18px;
	letter-spacing: 1.5px;
	width: 100px;
	height: 36px;
	background-color: #012998;
	color: #dde3fd;
	border: 1px solid #dde3fd;
	border-radius: 0;
	border-bottom-right-radius: 10px;
	cursor: pointer;
	transition: all 0.15s linear;
}

.add-btn:hover,
.add-btn:focus {
	background-color: #dde3fd;
	color: #012998;
}